import React from 'react';

import './index.scss';

const Flex = ({
  direction = 'row',
  halign = 'flex-start',
  valign = 'flex-start',
  className = '',
  grow = 0,
  shrink = 1,
  children,
}) => {
  const styles = {
    flexGrow: grow,
    flexShrink: shrink,
    flexDirection: direction,
    justifyContent: direction === 'row' ? halign : valign,
    alignItems: direction === 'row' ? valign : halign,
  };
  return (
    <div
      className={`flex ${className}`}
      style={className.match(/flex/) ? {} : styles}
    >
      {children}
    </div>
  );
};

export default Flex;



